<template>
    <div class="notebook-detail">
        <note-sidebar @updateNotes="val=>notes=val"> </note-sidebar>
        <div class="note-detail">
            <div class="note-empty" v-show="!curNote.id"> 请选择笔记 </div>

        <div v-show="curNote.id" class="note-detail-ct">
            <div class="note-bar">
            <span>创建日期:{{curNote.createdAtFriendly}}</span>
            <span>更新日期:{{curNote.updatedAtFriendly}}</span>
            <span>{{statusText}}</span>
            <span class="iconfont icon-delete" @click="ondeleteNote"></span>
            <span class="iconfont icon-fullscreen" @click="isShowPreview=!isShowPreview"  ></span>
        </div>


        <div class="note-title">
            <input type="text" v-model:value="curNote.title" placeholder="请输入标题" @input="onupdateNote" @keydown="statusText='正在输入'">
        </div>
        <div class="editor">
            <textarea v-show="!isShowPreview" @input="onupdateNote" v-model:value="curNote.content" placeholder="输入内容，支持md语法" >
                
            </textarea>
            <div class="preview markdown-body"  v-show="isShowPreview"  v-html="previewContent">
                
            </div>
        </div>



        </div>
    
        </div>
      

    </div>
</template>

<script>
import Auth from '@/api/auth'
import NoteSidebar from '@/components/NoteSidebar'
import Bus from '@/helpers/bus'
import Notes from '@/api/note'
import _ from 'loadsh'
import MarkdownIt from 'markdown-it'
import {mapState,mapGetters,mapMutations,mapActions} from 'vuex'

let md=new MarkdownIt()

export default {
    components:{
        NoteSidebar
    },
    data(){
        return{
            // curNote:{
              
            // },
            // notes:[],
            statusText:'笔记未改动',
            isShowPreview:false
        }
    },
        created(){
        Auth.getInfo().then(data=>{
            if(!data.isLogin){
                this.$router.push({path:'/login'})
            }
        })
    
    },
    
    beforeRouteUpdate (to, from, next) {
        // 在当前路由改变，但是该组件被复用时调用
        // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
        // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
        // 可以访问组件实例 `this`
        this.$store.commit('setCurNote',{curNoteId:to.query.noteId})
        console.log('beforeRouteUpdate enter') 
     //   this.curNote= this.notes.find( note=> note.id==to.query.noteId)
        next()
    },

    computed:{
        ...mapGetters([
          'notes',
          'curNote'
        ]),
        previewContent(){
            return md.render(this.curNote.content || '') 
        }
    },
  
    methods:{
        //  输入后更新   防抖动
        ...mapActions([
          'updateNote',
          'deleteNote'
        ]),
        onupdateNote:_.debounce(function(){
            this.updateNote(
                {noteId:this.curNote.id,title:this.curNote.title,content:this.curNote.content})
            .then(res=>{
                this.statusText='已保存'
                console.log(res)
            }).catch(err=>{
                 this.statusText='保存出错'
           })
        },300),
        ondeleteNote(){
             this.deleteNote( {noteId: this.curNote.id})
             .then(data=>{
                 this.$router.replace({path:'/note'})
                 })

        },
    }


}
</script>

<style lang="less" scoped>
.notebook-detail{
    display:flex;
    align-items: stretch;
    background-color:#fff;
    flex:1 ;
}
.note-detail {
  flex: 1;
  display: flex;
  flex-direction: column;

  .note-detail-ct {
    height: 100%;
  }

  .note-empty {
    font-size: 50px;
    color: #ccc;
    text-align: center;
    margin-top: 100px;
  }

  .note-bar {
    padding: 4px 20px;
    border-bottom: 1px solid #eee;
    &:after {
      content:'';
      display: block;
      clear: both;
    }
    span {
      font-size: 12px;
      color: #999;
      margin-right: 4px;
    }
    .iconfont{
      float: right;
      margin-left: 4px;
      font-size: 18px;
      cursor: pointer;
    }

  }

  .note-title{
    input,span {
      display: inline-block;
      width: 100%;
      border: none;
      outline: none;
      font-size: 18px;
      padding: 10px 20px;
    }
  }

  .editor {
    height: ~"calc(100% - 70px)";
    position: relative;
    textarea{
       
    }
    .vue-codemirror {
      height: ~"calc(100% - 25px)";

      .CodeMirror {
        height: 100%;
        padding: 10px;
      }
    }

  }
  textarea, .preview {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 20px;
  }

  textarea {
    border: none;
    resize: none;
    outline: none;
    font-size: 14px;
    font-family: 'Monaco', courier, monospace;
  }

  code {
    color: #f66;
  }
}  
</style>